import React, { useState } from 'react';
import {
  Container,
  Paper,
  TextField,
  Button,
  Typography,
  Box,
  CircularProgress,
  Alert,
  Tabs,
  Tab,
  Card,
  CardContent,
  Divider,
  Chip,
  IconButton,
  InputAdornment
} from '@mui/material';
import {
  YouTube as YouTubeIcon,
  Send as SendIcon,
  Email as EmailIcon,
  ContentCopy as CopyIcon,
  Check as CheckIcon
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { useWebSocket } from '../contexts/WebSocketContext';
import api from '../services/api';

function TabPanel({ children, value, index, ...other }) {
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography component="div">{children}</Typography>
        </Box>
      )}
    </div>
  );
}

export default function Home() {
  const { user } = useAuth();
  const { socket } = useWebSocket();
  const navigate = useNavigate();

  const [url, setUrl] = useState('');
  const [email, setEmail] = useState('');
  const [loading, setLoading] = useState(false);
  const [results, setResults] = useState(null);
  const [prompts, setPrompts] = useState([]);
  const [activeTab, setActiveTab] = useState(0);
  const [error, setError] = useState('');
  const [progress, setProgress] = useState(0);
  const [copied, setCopied] = useState({});

  React.useEffect(() => {
    loadSystemPrompts();
    
    if (socket) {
      socket.on('job:progress', (data) => {
        setProgress(data.progress);
      });
      
      socket.on('job:complete', (data) => {
        setResults(data.results);
        setLoading(false);
        setProgress(100);
      });
      
      socket.on('job:error', (data) => {
        setError(data.error);
        setLoading(false);
        setProgress(0);
      });
    }
    
    return () => {
      if (socket) {
        socket.off('job:progress');
        socket.off('job:complete');
        socket.off('job:error');
      }
    };
  }, [socket]);

  const loadSystemPrompts = async () => {
    try {
      const response = await api.get('/prompts');
      if (response.data.success) {
        setPrompts(response.data.data);
      }
    } catch (error) {
      console.error('Failed to load prompts:', error);
    }
  };

  const validateYouTubeUrl = (url) => {
    const patterns = [
      /^(https?:\/\/)?(www\.)?(youtube\.com\/watch\?v=|youtu\.be\/|youtube\.com\/embed\/)[\w-]+/,
      /^(https?:\/\/)?(www\.)?youtube\.com\/shorts\/[\w-]+/
    ];
    return patterns.some(pattern => pattern.test(url));
  };

  const handleSubmit = async () => {
    setError('');
    
    if (!url.trim()) {
      setError('YouTube URL을 입력해주세요.');
      return;
    }
    
    if (!validateYouTubeUrl(url)) {
      setError('올바른 YouTube URL을 입력해주세요.');
      return;
    }

    setLoading(true);
    setProgress(0);
    setResults(null);

    try {
      const response = await api.post('/summaries/youtube', {
        url: url.trim(),
        type: 'youtube',
        email: email.trim() || null,
        promptIds: prompts.map(p => p.id)
      }, {
        timeout: 120000 // 2 minutes timeout for YouTube processing
      });

      if (response.success) {
        const { results: apiResults, videoInfo } = response.data;
        
        // 결과를 화면에 표시
        if (apiResults) {
          // API 결과를 프롬프트 ID 기반으로 매핑
          const mappedResults = {};
          prompts.forEach(prompt => {
            const category = prompt.category || prompt.name.toLowerCase().replace(/\s+/g, '');
            mappedResults[prompt.id] = apiResults[category]?.content || '처리 중...';
          });
          
          setResults(mappedResults);
          setLoading(false);
          setProgress(100);
        }
        
        // 이메일이 입력된 경우 안내 메시지
        if (email.trim()) {
          setError('');
          alert(`처리가 완료되면 ${email}로 결과를 발송해드립니다.`);
        }
      }
    } catch (error) {
      setError(error.response?.data?.error || '처리 중 오류가 발생했습니다.');
      setLoading(false);
    }
  };

  const handleCopy = (promptId, text) => {
    navigator.clipboard.writeText(text);
    setCopied({ ...copied, [promptId]: true });
    setTimeout(() => {
      setCopied({ ...copied, [promptId]: false });
    }, 2000);
  };

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Paper elevation={3} sx={{ p: 4, borderRadius: 2 }}>
        <Box sx={{ textAlign: 'center', mb: 4 }}>
          <YouTubeIcon sx={{ fontSize: 60, color: 'error.main', mb: 2 }} />
          <Typography variant="h3" component="h1" gutterBottom>
            Lilys AI Clone
          </Typography>
          <Typography variant="h6" color="text.secondary">
            YouTube 동영상을 AI로 요약하고 분석합니다
          </Typography>
        </Box>

        <Box sx={{ mb: 3 }}>
          <TextField
            fullWidth
            label="YouTube URL"
            placeholder="https://www.youtube.com/watch?v=..."
            value={url}
            onChange={(e) => setUrl(e.target.value)}
            variant="outlined"
            size="large"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <YouTubeIcon color="error" />
                </InputAdornment>
              ),
            }}
            disabled={loading}
            sx={{ mb: 2 }}
          />

          <TextField
            fullWidth
            label="이메일 (선택사항)"
            placeholder="결과를 이메일로 받으시려면 입력하세요"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            variant="outlined"
            type="email"
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <EmailIcon />
                </InputAdornment>
              ),
            }}
            disabled={loading}
            helperText="이메일을 입력하시면 처리 완료 후 결과를 발송해드립니다"
            sx={{ mb: 3 }}
          />

          {error && (
            <Alert severity="error" sx={{ mb: 2 }}>
              {error}
            </Alert>
          )}

          <Button
            fullWidth
            variant="contained"
            size="large"
            onClick={handleSubmit}
            disabled={loading}
            startIcon={loading ? <CircularProgress size={20} /> : <SendIcon />}
            sx={{ py: 1.5 }}
          >
            {loading ? `처리 중... (${progress}%)` : 'AI 요약 시작'}
          </Button>
        </Box>

        {loading && (
          <Box sx={{ mt: 3 }}>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              AI가 콘텐츠를 분석하고 있습니다...
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Box sx={{ flex: 1 }}>
                <Box sx={{ width: '100%', mr: 1 }}>
                  <Box sx={{ 
                    width: '100%', 
                    height: 8, 
                    backgroundColor: '#e0e0e0',
                    borderRadius: 4,
                    overflow: 'hidden'
                  }}>
                    <Box sx={{ 
                      width: `${progress}%`, 
                      height: '100%',
                      backgroundColor: 'primary.main',
                      transition: 'width 0.3s ease'
                    }} />
                  </Box>
                </Box>
              </Box>
              <Typography variant="body2" color="text.secondary">
                {progress}%
              </Typography>
            </Box>
          </Box>
        )}

        {results && (
          <Box sx={{ mt: 4 }}>
            <Divider sx={{ mb: 3 }} />
            <Typography variant="h5" gutterBottom>
              요약 결과
            </Typography>

            <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 2 }}>
              <Tabs value={activeTab} onChange={handleTabChange} variant="scrollable">
                {prompts.map((prompt, index) => (
                  <Tab key={prompt.id} label={prompt.name} />
                ))}
              </Tabs>
            </Box>

            {prompts.map((prompt, index) => (
              <TabPanel key={prompt.id} value={activeTab} index={index}>
                <Card variant="outlined">
                  <CardContent>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                      <Typography variant="h6">{prompt.name}</Typography>
                      <Box>
                        <Chip 
                          label={prompt.category} 
                          size="small" 
                          color="primary" 
                          variant="outlined" 
                          sx={{ mr: 1 }}
                        />
                        <IconButton
                          size="small"
                          onClick={() => handleCopy(prompt.id, results[prompt.id] || '')}
                          title="복사"
                        >
                          {copied[prompt.id] ? <CheckIcon /> : <CopyIcon />}
                        </IconButton>
                      </Box>
                    </Box>
                    <Typography 
                      variant="body1" 
                      sx={{ 
                        whiteSpace: 'pre-wrap',
                        lineHeight: 1.8
                      }}
                    >
                      {results[prompt.id] || '결과를 생성하는 중입니다...'}
                    </Typography>
                  </CardContent>
                </Card>
              </TabPanel>
            ))}

            {user && (
              <Box sx={{ mt: 3, textAlign: 'center' }}>
                <Button
                  variant="outlined"
                  onClick={() => navigate('/history')}
                >
                  내 히스토리 보기
                </Button>
              </Box>
            )}
          </Box>
        )}

        {!user && (
          <Box sx={{ mt: 4, p: 3, bgcolor: 'grey.100', borderRadius: 2, textAlign: 'center' }}>
            <Typography variant="body1" gutterBottom>
              로그인하면 더 많은 기능을 사용할 수 있습니다
            </Typography>
            <Box sx={{ mt: 2, display: 'flex', gap: 2, justifyContent: 'center' }}>
              <Button variant="contained" onClick={() => navigate('/login')}>
                로그인
              </Button>
              <Button variant="outlined" onClick={() => navigate('/register')}>
                회원가입
              </Button>
            </Box>
          </Box>
        )}
      </Paper>
    </Container>
  );
}